nav{
    background-color: #1e4765;
    margin: 0;
    padding: 0;
    overflow: hidden;
    height: 50px;
  }
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    /* overflow: hidden; */
    font-size: 18px;
  }
  
  li.Main{
      float: left;
      padding-left: 50px;
  }
 img{
   margin-left: 250px;
   height: 45px;
   width: 45px;
   margin-top: 2.5px;
 }
  li a{
    display: inline-block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  
  li a:hover, .dropdown:hover .dropbtn {
    background-color: white;
    color: black;
  }
  
  .dropdown-content,.sub-dropdown-content{
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
  }
  
  li a.sub-dropbtn,.sub-dropdown-content a {
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
    position:relative;
  }
  
  .dropdown-content a.sub-dropbtn:hover,.sub-dropdown-content a:hover {
    background-color: white;
    color: black;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
    background-color: aqua;
  }
  
  .sub-dropdown:hover .sub-dropdown-content {
    display: block;
    background-color: aqua;
    left: 100%;
    top:0;
  }
  
  .arrow {
    border: solid white;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    margin-left: 5px;
  }
  .down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }
  
  .right {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    margin:0;
  }
  
  .dropdown:hover .arrow{
      border: solid rgb(3, 112, 145);
      border-width: 0 3px 3px 0;
      display: inline-block;
      padding: 3px;
  }
  .dropdown:hover .down{
      transform: rotate(-135deg);
      -webkit-transform: rotate(-135deg);
      transition: 0.3s ease;
  }
  
  .sub-dropdown:hover .arrow{
      border: solid rgb(126, 0, 151);
      border-width: 0 3px 3px 0;
      display: inline-block;
      padding: 3px;
  }
  
  .sub-dropdown:hover .right{
      transform: rotate(135deg);
      -webkit-transform: rotate(135deg);
      transition: 0.3s ease;
  }